import React from 'react';

// function handleMouseOver() {
//     return (document.body.style.backgroundColor = 'red');
// }

function Button ( type ) {
    let logo = '';
    let text = '';
    let link = 'https://google.com';
    switch (type.type){
        case "github":
            logo = './images/git.png';
            text = 'Github Logo';
            link = 'https://github.com/BluThaitanium';
            break;
        case "linkedin":
            logo = './images/link.png';
            text = 'Linkedin Logo';
            link = 'https://www.linkedin.com/in/phu-thai-673758178/';
            break;
        case "facebook":
            logo = './images/facebook.png';
            text = 'Facebook Logo';
            link = 'https://www.facebook.com/profile.php?id=100008653755528';
            break;
        case "email":
            logo = './images/email.png';
            text = 'Generic Envelope';
            link = 'https://mail.google.com/mail/u/0/?fs=1&tf=cm&source=mailto&to=phuthai450@gmail.com';
            break;
        default:
            break;
    };
    return (
        <a href={link} target="_blank" 
           rel="noreferrer"
        //    onMouseOver={handleMouseOver}
           >
                <img src={logo}
                width='100' height='100'
                alt={text}/>
        </a>
    );
};


export default Button;